
const keys = [
  ["A", "B", "C", "Ç", "D", "E", "F", "G", "Ğ", "H", "I", "İ"],
  ["J", "K", "L", "M", "N", "O", "Ö", "P", "R", "S", "Ş"],
  ["⏎", "T", "U", "Ü", "V", "Y", "Z", "⌫"],
];

const Klavye = ({ onKeyPress, cellColors, table, currentCell }) => {
  
  const getLetterColor = (tableCellColor) => {
    let color = "";
    for (let row = 0; row < table.length; row++) {
      for (let col = 0; col < table[row].length; col++) {
        if (table[row][col] === tableCellColor) {
          if (cellColors[row][col] === "#8BC34A") {
            return "#8BC34A";
          } else if (cellColors[row][col] === "#FFC107") {
            color = "#FFC107";
          } else if (cellColors[row][col] === "#ff6767ff") {
            color = "#ff6767ff";
          }
        }
      }
    }
    return color;
  };

  const getButtonProps = (k) => {
    const buttonColor = getLetterColor(k);
    if (k === "ENTER") {
      const isDisabled = !table[currentCell?.row]?.every(cell => cell !== "");
      return {
        style: {
          background: buttonColor || "#F2DBD5",
        },
          disabled: isDisabled,
        };
      }
      return {
        style: {
          background: buttonColor || "#F2DBD5",
        }
      };
    };
  

  return (
    <div className="klavye-cluster">
      <div className="button-cluster button-cluster-upper">
        {keys[0].map((k) => (
          <button 
          key={k} 
          onClick={() => onKeyPress(k)} 
          {...getButtonProps(k)}
          >
            {k}
          </button>
        ))}
      </div>
      <div className="button-cluster button-cluster-middle">
        {keys[1].map((k) => (
          <button 
          key={k} 
          onClick={() => onKeyPress(k)} 
          {...getButtonProps(k)}
          >
            {k}
          </button>
        ))}
      </div>
      <div className="button-cluster button-cluster-bottom">
        {keys[2].map((k) => (
          <button 
          key={k} 
          onClick={() => onKeyPress(k)} 
          className={["⏎", "⌫"].includes(k) ? "special-key" : ""}
          {...getButtonProps(k)}
          >
            {k}
          </button>
        ))}
      </div>
    </div>
  );
};

export default Klavye;